
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import MsgPrompt from './MsgPrompt'
import PopupMasker from './PopupMasker'
import PromptInput from './PromptInput'

export default {
name: 'PasswordManage',
data () {
return {
resetBody: {
visible: false,
tip: '重置交易密码需首先进行账户的实名认证',
cancelText: '暂不认证',
successText: '立即认证',
successFn: this.successFn,
cancelFn: this.cancelFn
},
newBody: {
visible: false,
tip: '您未设置交易密码，请设置',
cancelText: '取消',
successText: '确定'
},
lastTradePassword: '',
tradePassword: '',
first: true,
successTip: false
}
},
components: {
Cell,
Group,
PopupMasker,
PromptInput,
MsgPrompt
},
methods: {
tradePasswordReset () {
this.resetBody.visible = true
},
newTradePassword () {
this.newBody.visible = true
},
successFn () {
this.resetBody.visible = false
},
cancelFn () {
this.resetBody.visible = false
},
clearInput () {

},
toggleVisible () {

},
setCancel () {
this.tradePassword = ''
this.newBody.visible = false
},
setNext () {
if (this.first) {
this.lastTradePassword = this.tradePassword
this.$nextTick(() => {
this.tradePassword = ''
})
this.newBody.tip = '请再次输入刚才设置的交易密码'
this.first = false
}else {
if (this.tradePassword === this.lastTradePassword) {
this.newBody.visible = false
this.successTip = true
}else {
this.$vux.toast.show({
text: '两次输入不一致'
})
this.tradePassword = ''
}
}
},
confirmSuccess () {
this.successTip = false
this.tradePassword = ''
}
}
}



