import Masker from './Masker'

export default {
	name: 'PromptInput',
	data () {
		return {}
	},
	components: {
		Masker
	},
	methods: {
		cancel () {
			this.$emit('cancel')
		},
		success () {
			this.$emit('next')
		}
	},
	props: {
		body: {
			type: Object,
			required: true
		}
	}
}



