import Masker from './Masker'

export default {
	name: 'TextPrompt',
	data () {
		return {

		}
	},
	components: {
		Masker
	},
	methods: {
		btnClick () {
			this.$emit('confirm')
		}
	},
	props: {
		msg: {
			type: String,
			required: true
		},
		btnText: {
			type: String,
			required: true
		},
		visible: {
			type: Boolean,
			default: false
		}
	}
}



