export default {
	name: 'Prompt',
	data () {
		return {}
	},
	methods: {
		cancel () {
			this.$emit('cancel')
		},
		success () {
			this.$emit('success')
		}
	},
	props: {
		prompt: {
			type: Object,
			required: true
		}
	}
}



